<template>
  <div class="orderSuccess">
  	<square v-bind:order='order'></square>
  	<div class="line">
  		<span class='span1'>您预约的门店：</span>
  		<span class='span2'>启路汽车美容店（武汉汽车店）</span>
  	</div>
  	<div class="line">
  		<span class='span1'>套餐：</span>
  		<span class='span2'>小保养套餐A</span>
  	</div>
  	<div class="line">
  		<span class='span1'>联系人：</span>
  		<span class='span2'>张志凤</span>
  	</div>
  	<div class="line">
  		<span class='span1'>联系电话：</span>
  		<span class='span2' @click="cancel">13112345678</span>
  	</div>
    <router-link :to="{name:'orderConfirm',params: {userName:'1'}}"><btn v-bind:message='message1'></btn></router-link>
  	<div @click="cancel"><btn v-bind:message='message2' v-bind:color='color' ></btn></div>
  </div>
</template>

<script>
import btn from '@/components/btn'
import square from '@/components/square'

export default {
  name: 'orderSuccess',
  data(){
  	return{
  		message1:'立即支付',
  		message2:'取消预约',
  		color:'grey',
  		order:'预约成功'
  	}
  },
  components:{
  	btn,
  	square
  },
  methods:{
  	cancel(){
  		this.$confirm('是否要取消？',{
  			confirmButtonText:'确定',
  			cancelButtonText:'点错了',
  			type: 'warning'
  		}).then(() => {
          
        }).catch(() => {
            
        });
  	}
  }
}
</script>

<style >
	.orderSuccess{
		height: calc(100% - 4rem);
		background: #F5F5F5;
		padding-top: 1rem;
		
	}
	
	.orderSuccess .line{
		padding:0.5rem 5%;
		border-top:1px solid #F5F5F5;
		background: #fff;
		height:2rem;
		line-height: 2rem;
		font-size: 0.8rem;
		display: flex;
		justify-content: space-between;
	}
	.orderSuccess .line2{
		margin-top: 0.7rem;
	}
	.orderSuccess .span2{
		color:#8c8c8c;
	}
	.orderSuccess a{
		text-decoration: none;
	}
	.el-message-box__title{
		color:#fff!important;
	}
	.el-icon-warning{
		display: none;
	}
	.el-message-box{
		width:16rem!important;
	}
	.el-message-box__message{
		margin-left: 0!important;
		text-align: center;
	}
	.el-button--primary{
		background: #eb4452!important;
		border-color: #eb4452!important;
	}
</style>



