<template>
	<div class="square">
  		<div class='correct'><img  src="../assets/correct.png"/></div>
  		<p>{{order}}</p>
  	</div>
</template>

<script>
	export default{
		name:'square',
		props:['order']
	}
</script>

<style scoped>
	.square{
		height: 8rem;
		margin:0rem 5%;
		border-radius: 8px;
		background: #fff;
		-webkit-box-shadow:0 0 10px rgba(51,51,51, .5);  
	    -moz-box-shadow:0 0 10px rgba(51,51,51, .5);  
	    box-shadow:0 0 10px rgba(51,51,51, .5);  
	    margin-bottom: 1rem;
	}
	.correct{
		padding-top:1.5rem;
	}
	.square img{
		height: 2rem;
	}
	.square p{
		text-align: center;
		
	}
</style>


