<template>
	<div class="meu">
		<div v-for="(relation,index) in relations" class="item" @click="relationClick(index)" v-bind:class="{checked:index==nowIndex}">
			<router-link v-bind:to="relation.myLink">
			<icon v-bind:name="relation.name" scale="2.5"></icon>
			<div class="item">{{relation.tit}}</div>
			</router-link>
		</div>
	</div>
</template>
<script>

export default{
	name:'meu',
	data(){
		return{
			nowIndex:-1,
			relations:[
				{ tit:'加油',myLink:'/come',name:'meu1'},
				{ tit:'汽车服务',myLink:'/carServeice',name:'meu2'},
				{ tit:'活动',myLink:'/activity',name:'meu3'},
				{ tit:'我',myLink:'/personCenter',name:'meu4'},
			]
		}
	},
	methods:{
		relationClick:function(index){
			this.nowIndex =index;
		}
	},
}

</script>

<style scoped>
	.meu{
		height: 3rem;
		display: flex;
		justify-content: space-around;
		color:#444444;
		font-size: 0.8rem;
		border-top:1px solid #ccc ;
		position: absolute;
		bottom: 0;
		width: 100%;
		padding-top:0.3rem;
		background: #F5F5F5;
	}
	.meu img{
		width:1.5rem;
	}
	.meu .item{
		width: 4.5rem;
	}
	.meu a{
		text-decoration: none;
		color:#111;
	}
	.checked div,.checked .svg-icon{
		color:#42b983!important;
	}
</style>

/*{ tit:'汽车服务',myLink:'/carServeice',imgSrc:require('../assets/car.png')}, 如果是img 的话 可以用require()*/


