import Vue from 'vue'
import Router from 'vue-router'

/*加油*/
import come from '@/components/come/come'                                               /*加油首页*/
import comePay from '@/components/come/comePay'                                         /*支付页*/
import promote from '@/components/come/promote'                                         /*推广页*/
import allMerchant from '@/components/come/allMerchant'                                 /*所有商户页*/
import tombola from '@/components/come/tombola'                                         /*刮奖页*/
import merchant from '@/components/come/merchant'                                       /*商户详情页*/

/*汽车服务*/
import carServeice from '@/components/carServeice/carServeice'                          /*汽车服务*/
import carWash from '@/components/carServeice/carWash'					                        /*洗车*/
import advise from '@/components/carServeice/advise'                                    /*意见反馈*/
import orderConfirm from '@/components/carServeice/orderConfirm'                        /*订单确认*/
import maintain from '@/components/carServeice/maintain'                                /*预约小保养*/
import orderSuccess from '@/components/carServeice/orderSuccess'                        /*预约成功*/
import carPaySuccess from '@/components/carServeice/carPaySuccess'                      /*支付成功*/
/*活动*/
import activity from '@/components/activity/activity'
/*我*/
import personCenter from '@/components/personCenter/personCenter'                       /*个人中心*/
import personOrder from '@/components/personCenter/personOrder'                         /*我的订单*/
import personOrderCancel from '@/components/personCenter/personOrderCancel'             /*已取消订单*/
import personOrderComplete from '@/components/personCenter/personOrderComplete'         /*已完成订单*/
import personOrderIn from '@/components/personCenter/personOrderIn'                     /*预约中订单*/

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'come',
      component: come,
      meta: { navShow: true,name:'加油'}
    },
    {
      path: '/come',
      name: 'come',
      component: come,
      meta: { navShow: true,name:'加油'}
    },
    {
      path: '/comePay',
      name: 'comePay',
      component: comePay,
      meta: { navShow: false,name:'支付'}
    },
    {
      path: '/carServeice',
      name: 'carServeice',
      component: carServeice,
      meta: { navShow: true,name:'汽车服务'}
    },
    {
      path: '/activity',
      name: 'activity',
      component: activity,
      meta: { navShow: true,name:'活动'}
    },
    {
      path: '/personCenter',
      name: 'personCenter',
      component: personCenter,
      meta: { navShow: true,name:'个人中心'}
    },
    {
      path: '/personOrder',
      name: 'personOrder',
      component: personOrder,
      meta: { navShow: false,name:'我的订单'}
    },
    {
      path: '/personOrderCancel',
      name: 'personOrderCancel',
      component: personOrderCancel,
      meta: { navShow: false,name:'已取消订单'}
    },
    {
      path: '/personOrderComplete',
      name: 'personOrderComplete',
      component: personOrderComplete,
      meta: { navShow: false,name:'已完成订单'}
    },
    {
      path: '/personOrderIn',
      name: 'personOrderIn',
      component: personOrderIn,
      meta: { navShow: false,name:'预约中订单'}
    },
    {
      path: '/promote',
      name: 'promote',
      component: promote,
      meta: { navShow: false,name:'推广'}
    },
    {
      path: '/allMerchant',
      name: 'allMerchant',
      component: allMerchant,
      meta: { navShow: false,name:'所有商户'}
    },
    {
      path: '/tombola',
      name: 'tombola',
      component: tombola,
      meta: { navShow: false,name:'刮奖'}
    },
    {
      path: '/merchant',
      name: 'merchant',
      component: merchant,
      meta: { navShow: false,name:'启路汽车美容店'}
    },
    {
      path: '/carWash',
      name: 'carWash',
      component: carWash,
      meta: { navShow: false,name:'洗车门店'}
    },
    {
      path: '/advise',
      name: 'advise',
      component: advise,
      meta: { navShow: false,name:'意见反馈'}
    },
    {
      path: '/orderConfirm',
      name: 'orderConfirm',
      component: orderConfirm,
      meta: { navShow: false,name:'订单确认'}
    },
    {
      path: '/maintain',
      name: 'maintain',
      component: maintain,
      meta: { navShow: false,name:'预约小保养'}
    },
    {
      path: '/orderSuccess',
      name: 'orderSuccess',
      component: orderSuccess,
      meta: { navShow: false,name:'预约成功'}
    },
    {
      path: '/carPaySuccess',
      name: 'carPaySuccess',
      component: carPaySuccess,
      meta: { navShow: false,name:'支付成功'}
    }
    
  ]
})



