<template>
	<div class="orderConfirm">
		<div v-show="this.$route.params.userName=='2'">
			<div class="line">
				<span>标准洗车</span>
				<span class="span2">￥28</span>
			</div>
			<div class="line">
				<span class="span1">打蜡</span>
				<span class="span2">￥88</span>
			</div>
			<div class="line">
				<span class="span1">抛光</span>
				<span class="span2">￥188</span>
			</div>
		</div>
		<div class="line" v-show="this.$route.params.userName=='1'">
			<span class="span1">小套餐保养A</span>
			<span class="span2">￥999.00</span>
		</div>
		<div class="line line2">
			<span class="span1">额外消费</span>
			<span class="span2">请与店员核对金额后消费</span>
		</div>
		<div class="line line2">
			<span class="span1">红包抵扣</span>
			<span class="span2">暂无红包抵扣</span>
		</div>
		<router-link to='/carPaySuccess'  v-show='this.$route.params.userName=="1"'><btn v-bind:message='message'></btn></router-link>
		<btn v-bind:message='message' v-bind:order='pay'  v-show='this.$route.params.userName!="1"'></btn>
		<p class="p">买单仅限于到店支付，请确认金额后提交</p>
	</div>
	
</template>

<script>
	import btn from '@/components/btn'
	export default {
		name:'orderConfirm',
		data(){
			return{
				message:'',
				pay:'支付成功'
			}
		},
		components:{
			btn
		},
		created:function(){
			this.$route.params.userName=='1' ? this.message='去支付￥999（已节省20元）':this.message='去支付￥304（已节省50元）'
		}
	}
</script>

<style scoped="">
	.orderConfirm{
		height: calc(100% - 4rem);
		background: #F5F5F5;
		padding-top:1rem;
	}
	.orderConfirm .line{
		height:3.3rem;
		line-height: 3.3rem;
		font-size: 0.8rem;
		padding:0 1rem;
		border-bottom: 1px solid #F5F5F5;
		background: #fff;
		display: flex;
		justify-content: space-between;
	}
	.orderConfirm .line2{
		margin-top:1rem
	}
	.orderConfirm .span2{
		color: #8C8C8C;
	}
	.orderConfirm .p{
		color: #8C8C8C;
		font-size: 0.5rem;
	}
	.orderConfirm a{
		text-decoration: none;
	}
</style>


