<template>
	<div class="paySuccess">
		<square v-bind:order='pay'></square>
		<complete></complete>
		<p>售后服务联系：0219898202</p>
	</div>
</template>

<script>
import complete from '@/components/personCenter/personOrderComplete'
import square from '@/components/square'
export default{
	name:'paySuccess',
	data(){
		return{
			pay:'支付成功',
		}
	},
	components:{
		complete,
		square
	}
}
</script>

<style scoped>
	.paySuccess{
		height:calc(100% - 4rem);
		margin-top:1rem;
		background: #F5F5F5;
	}
	.paySuccess p{
		font-size: 0.5rem;
	}
</style>


