<template>
	<div class="advise">
		<p>问题和意见</p>
		<textarea name="" rows="" cols="" placeholder="请输入不少于10个字的描述"></textarea>
		<div class="upload">
			<p>请提供相关问题的截图或图片</p>
			<el-upload
			  class="upload-demo"
			  action="https://jsonplaceholder.typicode.com/posts/"
			  :on-preview="handlePreview"
			  :file-list="fileList2"
			  list-type="picture">
			  <el-button size="small" type="primary">点击上传</el-button>
			</el-upload>
		</div>
		<btn v-bind:message="message"></btn>
	</div>
	
</template>

<script>
	import btn from '@/components/btn'
	export default{
		name:'advise',
		data() {
	      return {
	        fileList2: [{name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}],
	        message:'支付',
	      };
	    },
	    components:{
	    	btn,
	    },
	    methods: {
	      handleRemove(file, fileList) {
	        console.log(file, fileList);
	      },
	      handlePreview(file) {
	        console.log(file);
	      }
	    }
	}
</script>

<style>
	.advise{
		height: calc(100% - 4rem);
		padding: 0.5rem 0;
		font-family: "微软雅黑";
	}
	.advise p{
		text-align: left;
		font-size: 0.5rem;
		padding: 0 1rem;
	}
	.advise textarea{
		background-color: #f2f2f2;
		border:1px solid #f2f2f2;
		width: calc(100% - 3rem - 2px);
		height: 8rem;
		resize: none;
		margin: 0 1rem;
		outline: none;
		padding:0.5rem;
		line-height: 1rem;
		font-size: 1.5rem;
	}
	.advise  textarea::-webkit-input-placeholder{
		text-align: center;
		line-height: 8rem;
		color: #c0c0c0;
		font-family:"微软雅黑";
		font-size: 0.6rem;
	}
	
	.advise .upload{
		margin: 2rem 0 ;
		height: 11rem;
		border-top:2px solid #F5F5F5;
		border-bottom: 2px solid #F5F5F5;	
	}
	.is-success a{
		display: none;
	}
	.el-upload-list__item{
		width: 20%;
	}
	.el-upload-list--picture .el-upload-list__item{
		border: none;
		padding-right: 0px;
	}
	.el-upload-list{
		display: flex;
		justify-content: flex-start;
	}
	.el-upload-list--picture .el-upload-list__item-status-label{
		background: initial;
		box-shadow:initial;
	}
	.el-upload-list--picture .el-upload-list__item .el-icon-check, .el-upload-list--picture .el-upload-list__item .el-icon-circle-check{
		color: #fff;
		background:#eb4452;
		height: 20px;
		width: 20px;
		border-radius: 20px;
		z-index: 999;
	}
	.el-upload-list__item .el-icon-close{
		color: #fff;
		background:#eb4452;
		height: 20px;
		width: 20px;
		border-radius: 20px;
		z-index: 999;
		top:2px;
		right: 2px;
		opacity: 1;
	}
	[class*=" el-icon-"], [class^=el-icon-]{
		line-height: 1.5;
		font-size: 12px;
	}
	.el-upload-list--picture .el-upload-list__item-status-label{
		z-index: 9999;
	}
	.el-upload-list--picture .el-upload-list__item-thumbnail{
		width:50px;
		height: 50px;
		margin-left: -61px;
	}
	.el-list-enter-active{
	}
</style>


