<template>
	<div class="btn" v-bind:class='color'>{{message}}</div>
</template>

<script>
	export default{
		name:'btn',
		props:['message','color']
		
	}
</script>

<style scoped="">
	.btn{
		width: calc(100% - 2rem );
		height: 2.5rem;
		border-radius: 6px;
		line-height: 2.5rem;
		text-align: center;
		font-size: 0.8rem;
		background: #eb4452;
		color:#fff;
		margin: 0.5rem 1rem;
	}
	.grey{
		background: #ccc!important;
	}
</style>


