<template>
	<div class="carWash">
		<div class="item1">
			<router-link to='/carWash'><div class="pic">洗车</div></router-link>
		</div>
		<shop></shop>
	</div>
</template>

<script>
	import shop from '@/components/carServeice/shop'
	export default{
		name:'carWash',
		components:{
			shop
		}
	}
</script>

<style scoped>
	.carWash{
		background: #F5F5F5;
		height:calc(100% - 3.3rem);
	}
	.item1{
		padding: 0.5rem;
		padding-top:1rem;
		justify-content: space-around;
		display: flex;
		background: #fff;
	}
	.pic{
		width:5rem;
		height:3.5rem;
		background: #111111;
		border-radius: 4px;
		color: #fff;
		opacity: 0.5;
		font-size: 0.5rem;
		line-height: 3.5rem;
		text-align: center;
	}
	
</style>


